header.about {
  .wrapper {
    #hero-section {
      p {
        order: -1;
        margin-top: 300px;
        font-size: 30px;
        font-weight: 700;
        text-transform: uppercase;
      }
      h1 {
        margin-top: 50px;
        font-size: 101px;
      }
    }
